let beginX = 0;
let beginY = 0;
let nowX = 0;
let nowY = 0;
//判断touch是否生效的flag
let flag = true;
export default {
  data: function() {
    return {
      touchStart: false,
      touchEnd: false
    };
  },
  methods: {
    backStart: function(e) {
      this.touchStart = true;
      this.touchEnd = false;
      flag = true;
      beginX = e.touches[0].screenX;
      nowX = beginX;
      beginY = e.touches[0].screenY;
      nowY = beginY;
    },
    backEnd: function() {
      this.touchStart = false;
      this.touchEnd = true;
      if (flag) {
        this.$router.go(-1);
      }
    },
    //如果手指按下后悔，从按钮上移开应该记录这件事
    isBack: function(e) {
      nowX = e.touches[0].screenX;
      nowY = e.touches[0].screenY;
      if (Math.abs(nowX - beginX) >= 50 || Math.abs(nowY - beginY) >= 50) {
        flag = false;
      }
    }
  }
};



